import React from "react";
import { Card } from "react-bootstrap";

interface RepoCardType {
    title: string,
    des: string,
    htmlUrl: string
}

export default function RepoCard(props : RepoCardType){
    
    return(
        <Card>
            <Card.Img />
            <Card.Body>
                <Card.Title>
                    {props.title}
                </Card.Title>
                <Card.Text>
                    {props.des}
                </Card.Text>
                <a href={props.htmlUrl} target="_blank" rel="noreferrer"><small>Learn more</small></a>
            </Card.Body>
        </Card>
    )
}